import React, { useEffect, useState } from "react";
import InsertDriveFileOutlinedIcon from "@material-ui/icons/InsertDriveFileOutlined";
import Axios from "axios";
import Loader from "../../../Components/Loader/Loader";

const UserProfilePicture = (props) => {
  const [file, setFile] = useState(null);
  const [filename, setFilename] = useState(null);
  const [key, setKey] = useState(null);
  let formData = new FormData();
  formData.append("caption", filename);
  formData.append("file", file);

  let today = new Date();
  let getDate =
    String(today.getUTCDate()).padStart(2, "0") +
    String(today.getUTCMonth() + 1).padStart(2, "0") +
    String(today.getUTCFullYear()).replace("20", "");

  const upload = () => {
    Axios.put(`https://ecplc2021.herokuapp.com/upload-picture/${props.id}`, {
      picture: getDate + "_" + filename,
    }).then((response) => {
      if (response.data.err) {
        props.setInitial(response.data.err);
      } else {
        Axios.post(
          "https://ecplc2021.herokuapp.com/file/upload-file",
          formData,
          {
            headers: {
              "content-type": "multipart/form-data",
            },
          }
        );
        props.setInitial(response.data.success);
        props.setMessage(response.data.success);
        setTimeout(() => {
          props.setMessage("");
          props.setInitial("");
        }, 5000);
      }
    });
  };
  return (
    <>
      <div className="profile-info">
        <div className="user-nav-wrapper-sub-header">
          Upload profile picture
        </div>

        <div className="profile-picture">
          <img
            src={
              "https://ecplc2021.herokuapp.com/file/filename/" + props.picture
            }
            alt="Profile picture"
          />
        </div>
        {/* <img
          src={"https://ecplc2021.herokuapp.com/file/filename/" + props.picture}
          alt="Profile picture"
        /> */}

        <div className={filename === null ? "hidden" : "actuals"}>
          <p className="footer-add-word">
            <InsertDriveFileOutlinedIcon
              className="material-document"
              fontSize="small"
            />
            <i>{filename}</i>
          </p>
          <span
            onClick={() => {
              setFilename(null);
              setFile(null);
              setKey(null);
            }}
          >
            <i className="far fa-times-circle"></i>
          </span>
        </div>

        <div className="custom-file-div">
          {" "}
          <input
            key={key}
            onChange={(e) => {
              setFilename(e.target.files[0].name);
              setFile(e.target.files[0]);
              setKey(e.target.files[0].name);
            }}
            type="file"
            className={filename !== null ? "hidden" : "custom-file-input"}
          />
        </div>

        <div
          onClick={upload}
          className={filename === null ? "hidden" : "actuals-btn"}
        >
          UPLOAD
        </div>
      </div>
    </>
  );
};

export default UserProfilePicture;
